body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Universal Styles */

html {
    font-size: 15px;
}

@media only screen and (min-width: 560px) {
    html {
        font-size: 18px;
    }
}

input:focus {
    outline: none;
}

/* Reusable Component - Image Container */

.image-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    background: #000;
}

.image-container img {
    width: 100%;
}

/* App Styles */

.App {
    font-family: 'Work Sans', sans-serif;
}

h1 {
    padding: .66rem 0;
    text-align: center;
    background-color: #cca353;
    font-family: Poppins, sans-serif;
    font-size: 1.8rem;
    color: #fff;
}
.BusinessList {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 4.4rem 10%;
}
.Business {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 16.66rem;
    margin: 0 .5rem 2.3rem .5rem;
}
  
.Business .image-container {
    height: 16.66rem;
    margin-bottom: 1rem;
}
  
.Business h2 {
    margin-bottom: .5rem;
    font-size: 1.2rem;
    font-weight: 600;
}
  
.Business-information {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
  
.Business-information p {
    font-size: .88rem;
    font-weight: 300;
    line-height: 1rem;
}
  
.Business-address {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
  
.Business-reviews {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    text-align: right;
}
  
.Business-reviews h3 {
    color: #CCA353;
    font-weight: 600;
}
  
.Business-reviews .rating {
    font-size: .88rem;
    line-height: 1rem;
}
.SearchBar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    background-image: url(/ravenous/static/media/background_search_mobile.5d0905e6.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 25rem;
}

.SearchBar-sort-options ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 2.22rem;
    color: #ffffff;
    margin-left: 2rem;
    margin-right: 2rem;
}

.SearchBar-sort-options li {
    cursor: pointer;
    width: 4.33rem;
    border-bottom: 1px solid #fff;
    padding: 0 2.58rem .33rem 2.58rem;
    line-height: 1.13;
    text-align: center;
    font-weight: 600;
    font-size: .83rem;
    transition: color .25s;
}

.SearchBar-sort-options li:hover {
    color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
    border-bottom: 1px solid #f0c36c;
    color: #f0c36c;
}

.SearchBar-fields {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 2.88rem;
}

.SearchBar-fields input {
    width: 21rem;
    padding: .66rem 1rem;
    border: 1px solid #fff;
    border-radius: 4px;
    margin-right: 0;
    margin-bottom: .86rem;
    font-size: .77rem;
    font-weight: 500;
}

.SearchBar-fields input:last-child {
    margin-right: 0;
}

.SearchBar-submit {
    text-align: center;
}

.SearchBar-submit a {
    border-radius: 4px;
    padding: .72rem 1.7rem;
    background-color: #cca353;
    color: #ffffff;
    font-weight: 600;
    transition: background-color .5s;
}

.SearchBar-submit a:hover {
    cursor: pointer;
    background-color: #a7874b;
}

@media only screen and (min-width: 560px) {
    .SearchBar {
        background-image: url(/ravenous/static/media/background_search_desktop.902df4c5.jpg);
    }
    
    .SearchBar-sort-options ul {
        margin-left: 0;
        margin-right: 0;
    }
    
    .SearchBar-fields {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-align-items: stretch;
                align-items: stretch;
    }
    
    .SearchBar-fields input {
        margin-right: 2.22rem;
        margin-bottom: 0;
    }
}
